@charset "utf-8";

*{margin: 0px; padding: 0px;}
ul,ol,li{list-style: none;}
body{
  font-family: "맑은 고딕", arial, sans-serif;
  font-size: 12px;
  color: #333;
}

h1, h2{text-align: center;}
h2{margin-bottom: 20px;}

.i_wrap{
  width: 400px;
  margin: 0 auto;
}
.i_wrap label{
  width: 90px;
  display: inline-block;
}
.i_wrap input[type=text]{
  width: 260px; height: 23px;
  margin: 6px 0;
  border: 1px solid #ccc;
  outline: none;
  font-size: 12px;
}
.i_wrap input[type=text]:focus{border: 1px solid #333;}
.i_wrap input[type=button]{
  width: 110px; height: 32px;
  margin: 10px 0 40px 90px;
  background-color: #009B75;
  color: #fff;
  border: 2px solid #00634a;
}

.wrap{
  width: 500px; height: 1108px;
  /* background-image: url({`${process.env.PUBLIC_URL}/img/phone.png`}); */
  margin: 10px auto;
  padding: 80px 50px 10px 50px;
  /* border: 3px solid #000; */
  position: relative;
  overflow: hidden;
  border-radius: 100px;
}
.product_list{
  width: 100%; height: 115px;
  padding: 10px 0;
  border-bottom: 1px solid #ccc;
}
.product_list:last-child{border-bottom: none;}
.product_list > li{display: flex;}
.product_list img{
  width: 30%;
}
.product_list .t_wrap{
  padding: 0 20px;
  width: 230px;
}
.product_list .t_wrap p{
  line-height: 18px;
  padding-bottom: 10px;
  font-size: 14px;
}
.product_list input{
  width: 80px; height: 30px;
  background-color: #009B75;
  color: #fff;
  border: 1px solid #00634a;
  transform: translateY(80px);
}

.mockup{
  width: 600px;
  margin: 0 auto;
  display: block;
  z-index: 10;
  position: absolute;
  top: 0; left: 0;
}
input{
  position: relative;
  z-index: 20;
  cursor: pointer;
}